.settings span {
  cursor: pointer;
}
.settings span select {
  cursor: pointer;
}
.settings input, .settings label {
  cursor: pointer;
}

.sf-menu.settings {
  float: right;
  position: relative;
  top: -20px;

  li {
    background-color: #333;
    font-size: 15px;
    a {
      color: #ddd;
      &:hover {
        text-decoration: none;
      }
    }
  }
}
.ym-wbox:after {
    clear: both;
    content: ".";
    display: block;
    height: 0;
    visibility: hidden;
}
.sf-menu {
    float: left;
}
